  -webkit-filter: contrast(-1);
          filter: contrast(-1);
-webkit-filter:grayscale(-20);
        filter:grayscale(-20)
}

.contact_form_main_section .contact_heading_div .contact_heading {
  font-size: 5rem;
  font-weight: 600;
  color: #f6f6f6;
  word-spacing: 40px;
  letter-spacing: 5px;
}
.contact_form_main_section .contact_heading_div .bs_logo_bg {
  width: 90px;
  height: 90px;
  margin-bottom: 30px;
  -webkit-animation: bs-logo-spin infinite 15s linear;
          animation: bs-logo-spin infinite 15s linear;
}
@-webkit-keyframes bs-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
@keyframes bs-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

